import requestOpBaseUrl from '../requestOpBaseUrl';
import doLogout from './doLogout';

export interface LogoutArgs {
  /**
   * 大家采网关地址
   * 用于获取平台信息
   */
  djcGatewayBaseUrl: string;
  /**
   * 注销后的跳转地址
   */
  logoutRedirectUrl: string;
  /**
   * 注销其他的第三方单点登录, 例如政采云
   */
  otherLogoutUrls?: Array<string>;
  /**
   * 目标域, 默认为当前请求域
   */
  origin?: string;
}

export default async ({
  djcGatewayBaseUrl,
  logoutRedirectUrl,
  otherLogoutUrls,
  origin,
}: LogoutArgs) => {
  const opBaseUrl = await requestOpBaseUrl(origin, djcGatewayBaseUrl);

  await doLogout(opBaseUrl, logoutRedirectUrl, otherLogoutUrls);
};
