import { AnyAction, Dispatch } from 'redux';
import setGlobalContextMe from './setGlobalContextMe';
import { ME_URL } from '../constants/url';
import { WHO_AM_I_HOST_KEY } from '../constants/whoAmIHostKey';
import request from '../utils/request';
import compatibilityMe from '../utils/compatibilityMe';
import parseUrl from '../utils/parseUrl';
import { CompatibleMeI } from '../types/Me';

export interface RequestMeArgsI {
  /**
   * 用于设置全局状态
   */
  dispatch: Dispatch<AnyAction>;

  /**
   * 登录目标域
   */
  origin?: string;

  /**
   * who am i, 默认为 origin -> host
   */
  whoAmIHost?: string;
}

export default async ({
  origin = window.location.origin,
  whoAmIHost,
  dispatch,
}: RequestMeArgsI) => {
  const originHost = parseUrl(origin).host;
  const _whoAmIhost = whoAmIHost || originHost;

  const response = await request(ME_URL(origin), {
    params: {
      [WHO_AM_I_HOST_KEY]: _whoAmIhost,
    },
  });

  const me = response.data;

  if (!me || Object.getOwnPropertyNames(me).length === 0) return Promise.reject();

  const cMe = compatibilityMe(response.data);

  /**
   * platDomain/gateway/user 始终返回此平台的用户信息, whoAmIHost 只作为鉴权使用
   * 如果 origin 与当前访问域名不同, 不写入全局状态
   * 即只存放'自己'平台的登录信息
   */
  if (window.location.origin.includes(origin)) setGlobalContextMe(dispatch, cMe);

  return cMe as CompatibleMeI;
};
