import { HeaderTokenArgsI } from './types';

export const getToken = (key: string, authScheme?: string) => {
  try {
    if (document.cookie && document.cookie.length > 0) {
      const cookies = document.cookie.split(/\s*;\s*/);
      if (cookies && cookies.length > 0) {
        const cookieAuth = cookies.find(cookie => cookie.startsWith(key));
        const auth = cookieAuth ? cookieAuth.split('=')[1] : null;
        return auth && authScheme ? `${authScheme} ${auth}` : auth;
      }
    }
    return null;
  } catch {
    return null;
  }
};

export const defaults = ({
  platDomain = window.location.host,
  platDomainKey = '__platDomain__',
  notSetPlatDomain = false,
  authCookieKey = 'Auth',
  authHeaderKey = 'Authorization',
  authScheme = 'Auth',
  notSetAuthHeader = false,
}: HeaderTokenArgsI = {}) => ({
  platDomain,
  platDomainKey,
  notSetPlatDomain,
  authCookieKey,
  authHeaderKey,
  authScheme,
  notSetAuthHeader,
});
