import requestMe, { RequestMeArgsI } from './requestMe';

export interface RequestMeTrySSOArgsI extends RequestMeArgsI {
  // typo, 为了兼容而保留
  whoAmIhost?: string;
}

/**
 * @deprecated
 * 不再需要客户端尝试单点登录, 建议使用 requestMe 代替,
 * 参数拼写错误修正: whoAmIhost -> whoAmIHost
 */
const requestMeTrySSO = ({
  origin = window.location.origin,
  whoAmIHost,
  // typo, 为了兼容而保留
  whoAmIhost,
  dispatch,
}: RequestMeTrySSOArgsI) => requestMe({ dispatch, origin, whoAmIHost: whoAmIHost || whoAmIhost });

export default requestMeTrySSO;
