import { requestMeTrySSO } from '..';
import { RequestMeTrySSOArgsI } from '../requestMeTrySSO';

export interface LoginByPageArgs extends RequestMeTrySSOArgsI {
  /**
   * 登录页面地址
   */
  loginPage: string;
}

export default async ({ loginPage, ...requestMeArgs }: LoginByPageArgs) => {
  try {
    /**
     * 先尝试获取当前用户
     */
    const me = await requestMeTrySSO(requestMeArgs);
    return me;
  } catch (e) {
    window.open(loginPage, '_top');
  }
};
