import { getToken } from './utils';

/**
 * 为 url 附加登录授权 token 参数
 */
export default (
  url: string,
  paramKey: string = '__auth__',
  authCookieKey: string = 'Auth'
): string => {
  const token = getToken(authCookieKey);

  return `${url}${url.indexOf('?') !== -1 ? '&' : '?'}${paramKey}=${token}`;
};
