import { MutableRefObject } from 'react';
import { AxiosError } from 'axios';
import buildLoginParams from './buildLoginParams';
import { LoginCommonArgs } from './types';
import authorize from './authorize';
import popupLoginBox from './popupLoginBox';
import requestMe from '../requestMe';
import requestOpBaseUrl from '../requestOpBaseUrl';

export interface LoginByPopupArgs extends LoginCommonArgs {
  /**
   * 取消登录事件处理
   */
  onCancelLogin?: () => void;

  /**
   * 关闭登录弹层, 由于登录之后需要重定向到登录成功页面, 所以关闭动作只有由引用来调用
   */
  closePopupRef?: MutableRefObject<Function>;
}

export default async ({
  origin = window.location.origin,
  whoAmIHost,
  // typo, 为了兼容而保留
  whoAmIhost,
  clientId,
  platId,
  dp,
  ui,
  registerNoHeader,
  title,
  djcGatewayBaseUrl,
  loginSuccessRedirectUri,
  limitOrgTypes,
  oauthState,
  onCancelLogin,
  closePopupRef,
  dispatch,
}: LoginByPopupArgs) => {
  const _whoAmIhost = whoAmIHost || whoAmIhost;

  try {
    const me = await requestMe({ dispatch, origin, whoAmIHost: _whoAmIhost });
    return me;
  } catch (e) {
    const params = await buildLoginParams({
      origin,
      whoAmIHost: _whoAmIhost,
      djcGatewayBaseUrl,
      clientId,
      platId,
      dp,
      ui,
      registerNoHeader,
      title,
      loginSuccessRedirectUri,
      limitOrgTypes,
      extraParams: oauthState,
    });

    const opBaseUrl = await requestOpBaseUrl(origin, djcGatewayBaseUrl);

    try {
      const { location, res } = await authorize(opBaseUrl, {
        response_type: 'code',
        client_id: params.client_id,
        redirect_uri: params.redirect_uri,
        scope: params.scope,
        options: params.options,
        state: JSON.stringify(params),
      });

      if (res === 0) {
        const me = await requestMe({ dispatch, origin, whoAmIHost: _whoAmIhost }).catch(() => {
          // eslint-disable-next-line no-console
          console.error('服务器错误, 登录成功, 但获取登录用户信息失败!');
        });
        return me;
      }

      if (location) return popupLoginBox(location, onCancelLogin, closePopupRef);
      // eslint-disable-next-line no-console
      console.log('login error, mode=popup, authorize not response location');
    } catch (error) {
      const _error = error as AxiosError;

      const message =
        _error?.response?.data?.error_message ||
        _error?.response?.data?.message ||
        _error.message ||
        '服务器端错误';

      // eslint-disable-next-line no-console
      console.error(message, error);
    }
  }
};
