import React, { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import loginByPage, { LoginByPageArgs } from '../function/login/loginByPage';
import LoginOrRegister, { LoginOrRegisterProps } from './LoginOrRegister';
import Login from './Login';

export type Props = Omit<LoginByPageArgs, 'dispatch'> & Omit<LoginOrRegisterProps, 'children'>;

export default (props: Props) => {
  const { loginPage } = props;

  const dispatch = useDispatch();

  const login = useCallback(() => {
    loginByPage({ ...props, dispatch });
  }, [dispatch, props]);

  return (
    <LoginOrRegister {...props} logoutRedirectUrl={loginPage}>
      <Login login={login} />
    </LoginOrRegister>
  );
};
