import { AxiosRequestConfig } from 'axios';
import { HeaderTokenArgsI } from './types';
import { defaults, getToken } from './utils';

export default (args?: HeaderTokenArgsI) => (config: AxiosRequestConfig) => {
  const {
    platDomain,
    platDomainKey,
    notSetPlatDomain,
    authCookieKey,
    authHeaderKey,
    authScheme,
    notSetAuthHeader,
  } = defaults(args);

  /**
   * add params
   */
  const params =
    /**
     * 1. 要求不设置
     * 2. platDomain 为空值
     * 3. 请求中已经设置 [platDomainKey] 参数
     * 时不再设置
     */
    notSetPlatDomain || !platDomain || (config.params && config.params[platDomainKey])
      ? config.params
      : { ...config.params, [platDomainKey]: platDomain };

  /**
   * add headers
   */
  const token = getToken(authCookieKey, authScheme);

  const headers =
    /**
     * 1. 要求不设置
     * 2. cookie 中没有获取到 token
     * 3. 请求中已设值相应请求头
     * 时不再设置
     */
    notSetAuthHeader || !token || config.headers[authHeaderKey]
      ? config.headers
      : { ...config.headers, [authHeaderKey]: token };

  return { ...config, params, headers };
};
