import React, { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import loginByPopup, { LoginByPopupArgs } from '../function/login/loginByPopup';
import LoginOrRegister, { LoginOrRegisterProps } from './LoginOrRegister';
import Login from './Login';

export type Props = Omit<LoginByPopupArgs, 'dispatch'> & Omit<LoginOrRegisterProps, 'children'>;

export default (props: Props) => {
  const dispatch = useDispatch();

  const login = useCallback(() => {
    loginByPopup({ ...props, dispatch });
  }, [dispatch, props]);

  return (
    <LoginOrRegister {...props}>
      <Login login={login} />
    </LoginOrRegister>
  );
};
