import React from 'react';
import { RegisterArgs } from '../function/register';
import LoginOrRegisterContext from './LoginOrRegisterContext';
import Me from './Me';
import Register from './Register';
import { CommonProps } from './CommonProps';
import { LogoutArgs } from '../function/logout';
import useTryGetMe from './hooks/useTryGetMe';
import Or from './Or';

export interface LoginOrRegisterProps
  extends Omit<LogoutArgs, 'logoutRedirectUrl'>,
    RegisterArgs,
    CommonProps {
  logoutRedirectUrl?: string;
  children: React.ReactNode;
}

const LoginOrRegister = (props: LoginOrRegisterProps) => {
  const { className, children } = props;

  const me = useTryGetMe();

  return (
    <LoginOrRegisterContext.Provider value={props}>
      <div className={className}>
        {me ? (
          <Me me={me} />
        ) : (
          <>
            {children}
            <Or>或</Or>
            <Register />
          </>
        )}
      </div>
    </LoginOrRegisterContext.Provider>
  );
};

export default LoginOrRegister;
