import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import useMe from '../../hooks/useMe';
import { requestMe } from '../../function';

export default () => {
  const me = useMe();

  const dispatch = useDispatch();

  /**
   * 如果在全局状态中没有, 尝试请求一次
   */
  useEffect(() => {
    if (!me) {
      requestMe({ dispatch }).catch(() => {});
    }
  }, [me, dispatch]);

  return me;
};
