import * as cache from './cache';

/**
 * 从缓存中读取 token
 * @returns - {tokenParamKey: token} | null
 *
 * @deprecated
 * 旧的(第三方cookies)解决方案, 从 url 中获取 token 并通过header 携带 token, 不再建议使用
 * 请使用 TokenInterceptor 代替
 */
export default () => {
  const tpk = cache.getTokenParamKey();

  const token = cache.getToken();

  return token ? { [tpk]: token } : null;
};
